<template>

  <!---->

  <!---->
  <Header />

  <div class="book-background">
    <!--  -->
    <div id="videoContainer">
      <video class="fullscreenVideo" id="kotoba" playsinline autoplay muted loop>
        <source src="../assets/video/background.mp4" type="video/mp4">
      </video>
    </div>
  </div>


  <!--  -->
  <div class="search-interface">
    <!--  -->
    <div class="side-bar">
      <div class="user-info">
        <p class="user-name">{{ userName }}</p> <!---->

        <div class="tabs"> <!---->
          <button :class="{ active: activeTab === 'search' }" @click="setActiveTab('search')"></button>
          <button :class="{ active: activeTab === 'Myfavorited' }" @click="setActiveTab('Myfavorited')"></button>
          <button :class="{ active: activeTab === 'contribution' }" @click="setActiveTab('contribution')"></button>
          <button :class="{ active: activeTab === 'new' }" @click="setActiveTab('new')"></button>

          <button @click="TestPage"></button> <!---->
          <button @click="ErrorPage"></button> <!---->

          <button :class="{ active: activeTab === 'home' }" @click="GoToHome"></button>

        </div>
      </div>
    </div>

    <div class="top-banner">
      <!--
            <button class="user-button" @click="Gotouserpage">  
                <img :src="current" alt="User" />   
            </button>
            -->
    </div>


    <!---->
    <div v-if="activeTab === 'search'">
      <div class="search-logo"> <!--logo-->
      </div>

      <div class="search-box"> <!---->
        <input type="text" v-model="searchQuery" placeholder="" class="search-input" />
        <button @click="searchDatabase" class="search-button"></button>
      </div>
    </div>
    <!---->

    <!---->
    <div v-if="activeTab === 'Myfavorited'" class="favorite-plants-container">
      <h2 class="favorite-title"></h2>
      <ul class="favorite-plants-list">
        <li v-for="(item, index) in paginatedFavoritePlants" :key="index" class="favorite-plant-item">
          <span @click="viewPlantDetail(item.Plantid, item.Plantname)" class="plant-name">{{ item.Plantname }}</span>
          <button @click="removeFromFavorites(item.id)" class="remove-button"></button>
        </li>
      </ul>
      <div class="pagination">
        <button @click="prevPage" :disabled="currentPage === 1"></button>
        <span> {{ currentPage }} / {{ totalPages }}</span>
        <button @click="nextPage" :disabled="currentPage === totalPages"></button>
      </div>
    </div>
    <!---->


    <!---->
    <div v-if="activeTab === 'contribution'" class="favorite-plants-container">
      <h2 class="search-contribution-title"></h2>
      <ul class="search-contribution-list">
        <li v-for="(item, index) in paginatedContributions" :key="index" class="search-contribution-item">
          <span class="search-contribution-plant-name"
            @click="item.status === '' ? viewPlantDetail(item.Plantid, item.Plantname) : null"
            :class="{ clickable: item.status === '' }">
            {{ item.Plantname }}</span>
          <span class="review-status"
            :class="{ 'status-pending': item.status === '', 'status-approved': item.status === '', 'status-rejected': item.status === '' }">
            {{ item.status }}
          </span>
        </li>
      </ul>
      <div class="pagination">
        <button @click="prevContributionPage" :disabled="contributionPage === 1"></button>
        <span> {{ contributionPage }} / {{ contributionTotalPages }}</span>
        <button @click="nextContributionPage" :disabled="contributionPage === contributionTotalPages"></button>
      </div>
      <div class="search-contribution-button-container">
        <button class="search-contribution-button" @click="GoToContributionPage"></button>
      </div>
    </div>


    <!---->
    <div v-if="activeTab === 'new'" class="new-plants-container">
      <h2 class="new-plants-title"></h2>
      <ul class="new-plants-list">
        <li v-for="(item, index) in paginatedNewPlants" :key="index" class="new-plant-item">
          <div class="new-plant-header">
            <span @click="viewPlantDetail(item.Plantid, item.Plantname)" class="new-plant-name">{{ item.Plantname
              }}</span>
            <span class="update-time">{{ item.updateTime }}</span>
          </div>
        </li>
      </ul>
      <div class="pagination">
        <button @click="prevNewPlantsPage" :disabled="newPlantsPage === 1"></button>
        <span> {{ newPlantsPage }} / {{ newPlantsTotalPages }}</span>
        <button @click="nextNewPlantsPage" :disabled="contributionPage === newPlantsTotalPages"></button>
      </div>
    </div>
    <!---->

  </div>
  <Footer />
</template>

<script>
import { useRouter } from 'vue-router';
import { ref, onMounted } from 'vue';
import { mapState, mapGetters, mapActions } from 'vuex';
import Header from '@/home/Header.vue';
import Footer from '@/home/Footer.vue';
export default {
  name: "Search",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      buttonImageUrl: '../catalog/images/user_example.png',  // 
      userName: 'Wuhuairline',// ,
      searchQuery: '',   //
      activeTab: 'search',  //

      favoritePlants: [], //
      currentPage: 1, // 
      itemsPerPage: 10, // 

      contributions: [], //
      contributionPage: 1, //
      contributionItemsPerPage: 8, //

      newPlants: [], // 
      newPlantsPage: 1, // 
      newPlantsItemsPerPage: 8, // 

    };
  },
  mounted() {
    window.scrollTo(0, 0);
    this.loadFavorites();
    this.loadContributions();
    this.loadNewPlants();
  },

  computed: {
    ...mapState({
      currentUser: state => state.currentUser, // Vuex store currentUser
      userAvatar: state => state.userAvatar //  userAvatar
    }),
    ...mapGetters(['userAvatar']),  // userAvatar
    totalPages() {
      return Math.ceil(this.favoritePlants.length / this.itemsPerPage);
    },
    paginatedFavoritePlants() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.favoritePlants.slice(start, end);
    }, //

    contributionTotalPages() {
      return Math.ceil(this.contributions.length / this.contributionItemsPerPage);
    },

    paginatedContributions() {
      const start = (this.contributionPage - 1) * this.contributionItemsPerPage;
      const end = start + this.contributionItemsPerPage;
      return this.contributions.slice(start, end);
    },//

    newPlantsTotalPages() {
      return Math.ceil(this.newPlants.length / this.newPlantsItemsPerPage);
    },

    paginatedNewPlants() {
      const start = (this.newPlantsPage - 1) * this.newPlantsItemsPerPage;
      const end = start + this.newPlantsItemsPerPage;
      return this.newPlants.slice(start, end);
    },//
  },  //
  methods:
  {
    toggleUserMenu() {
      this.showUserMenu = !this.showUserMenu;
    },
    handleUserAvatarClick() {
      if (this.currentUser.role === 'guest') {
        this.$router.push('/login'); // guest
      } else {
        this.goToUserProfile(); // 
      }
    },

    goToUserProfile() {
      this.$router.push('/userprofile');
    }, //
    goToCommunity() {
      this.$router.push('/community');
    },
    goToCatalog() {
      this.$router.push('/catalog');
    },
    goHome() {
      this.$router.push('/home');
    },
    goToAdminPanel() {
      this.$router.push('/adminpanel');
    },
    GoToHome() {
      this.$router.push('/catalog');
    },//

    GoToContributionPage() {
      this.$router.push('/Contribution');
    },//

    searchDatabase() {
      console.log(`Searching for: ${this.searchQuery}`);
    }, //,  searchQuery 

    setActiveTab(tabName) {
      this.activeTab = tabName;
    },  //

    loadFavorites() {
      this.favoritePlants = [
        { id: 1, Plantname: '' },
        { id: 2, Plantname: '' },
        { id: 3, Plantname: '' },
        { id: 4, Plantname: '' },
        { id: 5, Plantname: '' },
        { id: 6, Plantname: '' },
        { id: 7, Plantname: '' }, // 
        { id: 8, Plantname: '' },
        { id: 9, Plantname: '' },
        { id: 10, Plantname: '' },
        { id: 10, Plantname: '' },
        // 
      ];

    },  //


    loadContributions() {
      this.contributions = [
        { id: 1, Plantname: '', status: '' },
        { id: 2, Plantname: '', status: '' },
        { id: 3, Plantname: '', status: '' },
        { id: 4, Plantname: '', status: '' },
        { id: 5, Plantname: '', status: '' },
        { id: 6, Plantname: '', status: '' },
        { id: 7, Plantname: '', status: '' },
        { id: 8, Plantname: '', status: '' },
        { id: 9, Plantname: '', status: '' },
        { id: 10, Plantname: '', status: '' },
        // ...
      ];
    },  //

    loadNewPlants() {
      this.newPlants = [
        { id: 1, Plantname: '', updateTime: '202491' },
        { id: 2, Plantname: '', updateTime: '2024830' },
        { id: 3, Plantname: '', updateTime: '2024825' },
        { id: 4, Plantname: '', updateTime: '2024820' },
        { id: 5, Plantname: '', updateTime: '2024815' },
        { id: 6, Plantname: '', updateTime: '2024810' },
        { id: 7, Plantname: '', updateTime: '202485' },
        { id: 8, Plantname: '', updateTime: '202481' },
        { id: 9, Plantname: '', updateTime: '2024728' },
        { id: 10, Plantname: '', updateTime: '2024720' },
        // ...
      ];
    }, // 


    removeFromFavorites(plantId) {
      const plantToRemove = this.favoritePlants.find(plant => plant.id === plantId); //
      this.favoritePlants = this.favoritePlants.filter(plant => plant.id !== plantId);
      console.log(`: ${plantToRemove.Plantname}`);
    },  //


    TestPage() {
      this.$router.push('/information');
    }, //,

    ErrorPage() {
      this.$router.push('/FindError');
    },

    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    }, //

    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    }, //

    prevContributionPage() {
      if (this.contributionPage > 1) {
        this.contributionPage--;
      }
    },
    nextContributionPage() {
      if (this.contributionPage < this.contributionTotalPages) {
        this.contributionPage++;
      }
    },

    prevNewPlantsPage() {
      if (this.newPlantsPage > 1) {
        this.newPlantsPage--;
      }
    },

    nextNewPlantsPage() {
      if (this.newPlantsPage < this.newPlantsTotalPages) {
        this.newPlantsPage++;
      }
    },


    viewPlantDetail(plantId, plantName) {
      this.$router.push(`/plant/${plantId}`);
      console.log(`: ${plantName}`);
    },     // ,
  }


};
</script>




