<script>
import { defineComponent } from 'vue';
import { mapGetters, mapActions } from 'vuex';
import Header from '@/home/Header.vue';
import Footer from '@/home/Footer.vue';
export default {
  components: {
    Header,
    Footer,
  },
  name: "Community",
  data() {
    return {
      buttonImageUrl: '../catalog/images/user_example.png',  // 
      userName: 'Wuhuairline', // ,
      activeTab: "HotPosts",
      isUserInfoVisible: false, // 

      allPosts: [],
      userPosts: [],
      userScore: [],
      postComment: [],

      currentPage: 1,

      scorePerPage: 5,// 
      // 
      title: '',
      content: '',
      //
      showModal: false,
      selectedPost: null,
      newComment: '',
      currentPage: 1,

      itemsPerPage: 10,
      showDeleteConfirm: false // 
    };
  },

  mounted() {
    window.scrollTo(0, 0);
    this.startCarousel();
    this.getAllPosts();
    this.getUserPosts();
    this.getUserScore();
  },
  computed: {
    ...mapGetters({
      currentUser: 'getUserInfo', // 
      isAdmin: 'isAdmin',
    }),
    paginatedScores() {
      const start = (this.currentPage - 1) * this.scorePerPage;
      const end = this.currentPage * this.scorePerPage;
      return this.userScore.slice(start, end); // 
    },
    totalPages() {
      return Math.ceil(this.userScore.length / this.scorePerPage); // 
    },

    paginatedPosts() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = this.currentPage * this.itemsPerPage;
      return this.allPosts.slice(start, end);
    },
    // 
    totalPagesCount() {
      return Math.ceil(this.allPosts.length / this.itemsPerPage);
    }
  },
  methods:
  {
    ...mapActions(['logout']),
    async getAllPosts() {
      try {
        const result = await this.$store.dispatch('fetchAllPosts');
        console.log(':', result);
        this.allPosts = result;
      } catch (error) {
        console.error(':', error);
      }
    },
    async getUserPosts() {
      try {
        const result = await this.$store.dispatch('fetchUserPosts', this.currentUser.userId);
        console.log(':', result);
        this.userPosts = result;
      } catch (error) {
        console.error(':', error);
      }
    },
    async getUserScore() {
      try {
        const result = await this.$store.dispatch('fetchScoreRank');
        console.log(':', result);
        this.userScore = result;
      } catch (error) {
        console.error(':', error);
      }
    },
    async fetchPostComments(articleId) {
      try {
        const response = await this.$store.dispatch('fetchCommentsByPost', articleId);
        this.postComment = response; // 
      } catch (error) {
        console.error(':', error);
      }
    },
    async searchDatabase() {
      console.log('');  // 

      if (this.searchQuery.trim() === '') {
        alert('');
        this.getAllPosts();
        return;
      }

      try {
        console.log(':', this.searchQuery);  // 
        const response = await this.$store.dispatch('searchPosts', this.searchQuery);
        console.log(':', response);  // 
        if (response) {
          //console.log(':', response);  // 
          this.allPosts=response;
          this.currentPage = 1;  // 
        }
      } catch (error) {
        console.error(':', error);  // 
        alert('');
      }
    },

    handleUserAvatarClick() {
      if (this.currentUser.role === 'guest') {
        this.$router.push('/login'); // guest
      } else {
        this.goToUserProfile(); // 
      }
    },
    setActiveTab(tabName) {
      this.activeTab = tabName;
    },  //

    goToDetail(id) {
      //  Vue Router 
      this.$router.push({ name: 'PostDetail', params: { id } });

    },
    startCarousel() {
      setInterval(this.nextSlide, 3000);
    },

    async submitPost() {
      // 
      if (!this.title || !this.content) {
        alert('');
        return;
      }

      try {
        //  Vuex  addPost action
        const success = await this.$store.dispatch('addPost', {
          title: this.title,
          content: this.content
        });

        if (success) {
          alert('!');
          this.clearForm(); // 
        } else {
          alert('');
        }
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },
    discard() {
      this.clearForm();
    },
    clearForm() {
      this.title = '';
      this.content = '';
    },

    Gotouserpage() {
      this.$router.push('/userprofile');
    }, //

    GotoPostDetil() {
      this.$router.push('/postDetail');
    }, //

    GoToMycomment() {
      this.$router.push('/myComment');
    },

    GotoMyfavourite() {
      this.$router.push('/myfavourite');
    }, //
    GoToHome() {
      this.$router.push('/home');
    },//


    summarizeContent(content) {
      return content.length > 100 ? content.slice(0, 100) + '...' : content;
    },
    showPostDetails(post) {
      this.selectedPost = post;
      this.showModal = true;
      this.fetchPostComments(post.articleId);
    },
    closeModal() {
      this.showModal = false;
      this.selectedPost = null;
    },
    async addComment() {
      if (this.newComment.trim() === '') return;
      try {
        //  Vuex  addComment action
        const success = await this.$store.dispatch('addComment', {
          articleId: this.selectedPost.articleId,
          content: this.newComment
        });

        if (success) {
          // 
          this.fetchPostComments(this.selectedPost.articleId)
          this.newComment = ''; // 
          console.log('');
        } else {
          console.error('');
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    deletePost() {
      this.showDeleteConfirm = true; // 
    },
    confirmDelete() {
      const index = this.visiblePosts.findIndex(post => post.id === this.selectedPost.id);
      if (index !== -1) {
        this.visiblePosts.splice(index, 1);
        this.closeModal();
      }
      this.showDeleteConfirm = false; // 
    },
    cancelDelete() {
      this.showDeleteConfirm = false; // 
    },
    navigateToPrevious() {
      if (this.currentPage > 1) this.currentPage--;
    },
    navigateToNext() {
      if (this.currentPage < this.totalPagesCount) this.currentPage++;
    }



  },

};
</script>




