<script>
import { defineComponent } from 'vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  name: "Mycomment",
  data() {
    return {
      buttonImageUrl: '../catalog/images/user_example.png',  // 
      userName: 'Wuhuairline', // ,
      activeTab: "Mycomment",
      isUserInfoVisible: false, // 

      userComment:[],
      allPosts:[],
      postComment:[],


      // 
      showModal: false,
      selectedComment: null,
      newComment: '',
      currentPage: 1,


      itemsPerPage: 5,
      showDeleteConfirm: false,// 

      commentsData: [],

    };
  },

  mounted() {
    window.scrollTo(0, 0);
    this.getUserComment();
    this.getAllPosts();
  },
  computed: {
    ...mapGetters({
      currentUser: 'getUserInfo', // 
      isAdmin: 'isAdmin',
    }),
    // 
    totalPagesCount() {
      return Math.ceil(this.userComment.length / this.itemsPerPage);
    },

    paginatedComments() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = this.currentPage * this.itemsPerPage;
      return this.userComment.slice(start, end);
    },
  },
  methods:
  {
    ...mapActions(['logout']),

    async getUserComment() {
      try {
        const result = await this.$store.dispatch('fetchCommentsByUser', this.currentUser.userId);
        console.log(':', result);
        this.userComment = result;
      } catch (error) {
        console.error(':', error);
      }
    },
    async getAllPosts() {
      try {
        const result = await this.$store.dispatch('fetchAllPosts');
        console.log(':', result);
        this.allPosts = result;
      } catch (error) {
        console.error(':', error);
      }
    },
    async fetchPostComments(articleId) {
      try {
        const response = await this.$store.dispatch('fetchCommentsByPost', articleId);
        this.postComment = response; // 
      } catch (error) {
        console.error(':', error);
      }
    },
    async addComment() {
      if (this.newComment.trim() === '') return;
      try {
        //  Vuex  addComment action
        const success = await this.$store.dispatch('addComment', {
          articleId: this.selectedPost.articleId,
          content: this.newComment
        });

        if (success) {
          // 
          this.fetchPostComments(this.selectedPost.articleId)
          this.newComment = ''; // 
          console.log('');
          this.getUserComment();
        } else {
          console.error('');
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    handleUserAvatarClick() {
      if (this.currentUser.role === 'guest') {
        this.$router.push('/login'); // guest
      } else {
        this.goToUserProfile(); // 
      }
    },
    setActiveTab(tabName) {
      this.activeTab = tabName;
    },  //


    summarizeContent(content) {
      return content.length > 100 ? content.slice(0, 100) + '...' : content;
    },

    GotoMyfavourite() {
      this.$router.push('/myfavourite');
    }, //

    Gotouserpage() {
      this.$router.push('/userprofile');
    }, //

    GotoPostDetil() {
      this.$router.push('/postDetail');
    }, //

    goToCommunity() {
      this.$router.push('/community');
    }, //


    GoToHome() {
      this.$router.push('/home');
    },//
    navigateToPrevious() {
      if (this.currentPage > 1) this.currentPage--;
    },
    navigateToNext() {
      if (this.currentPage < this.totalPagesCount) this.currentPage++;
    },
    getPostTitle(postId) {
      const post = this.postsData.find(p => p.id === postId);
      return post ? post.title : '';
    },
    showPostDetails(postId) {
      this.selectedPost = this.allPosts.find(p => p.articleId === postId);
      this.showModal = true;
      this.fetchPostComments(postId);
    },
    closeModal() {
      this.showModal = false;
      this.selectedPost = null;
    },
    deletePost() {
      this.showDeleteConfirm = true;
    },
    confirmDelete() {
      this.$store.dispatch('deleteComment',  this.selectedComment.commentId);
      this.userComment = this.userComment.filter(p => p.commentId !== this.selectedComment.commentId);
      this.showDeleteConfirm = false;
    },
    cancelDelete() {
      this.showDeleteConfirm = false;
    },
    deleteComment(comment) {
      this.selectedComment = comment;
      this.showDeleteConfirm = true;
    },
  },

};
</script>




