          <!--  -->
          <div class="edit-button-container">
            <button v-if="!editing" class="edit-button" @click="editUserInfo"></button>

            <div v-if="editing" class="edit-actions">
              <button class="confirm-button" @click="confirmEdit"></button>
              <button class="cancel-button" @click="cancelEdit"></button>

            </div>

          </div>
          <button class="change-password-button" @click="showChangePasswordDialog = true"></button>
          <div class="logout-button-container">
            <button class="logout-button" @click="performLogout"></button>
          </div>
        </div>




      </div>


      <div class="right-panel">
        <!--  -->
        <!--  v-for  -->

        <!--  -->
        <div class="dropdown-container">
          <div class="dropdown-text" @click="toggleDropdown">
            {{ selectedText }}
            <span :class="{ 'triangle': true, 'triangle-rotate': showDropdown }">&#9662;</span>
          </div>

          <!--  -->
          <ul v-if="showDropdown" class="dropdown-menu">
            <li @click="selectOption('')"></li>
            <li @click="selectOption('')"></li>
            <li @click="selectOption('')"></li>
          </ul>
        </div>

        <!--  -->
        <hr class="divider">


        <!--  -->
        <div v-if="selectedText === ''" class="selectedoption">

          <!--  -->
          <div v-for="post in posts" :key="post.articleId" class="post" @click="openPostDialog(post)">
            <h3 class="post-title">{{ post.title }}</h3>
            <p class="post-content">{{ post.content }}</p>
            <p class="post-meta">
              : {{ post.publishedDate }}
              <!-- <span v-if="post.article_type === 'announcement'"> | : </span>
      <span v-else> | : </span> -->
            </p>
            <!--  @click.stop  -->
            <button class="delete-button" @click.stop="confirmDelete(post)"></button>
          </div>
        </div>


        <!--  -->
        <div v-if="selectedText === ''" class="selectedoption">

          <!--  -->
          <div v-for="favorite in userFavors" :key="favorite.articleId" class="post"
            @click="openFavoriteDialog(favorite)">
            <h3 class="post-title">{{ favorite.title }}</h3>
            <p class="post-content">{{ favorite.content }}</p>
            <p class="post-meta">: {{ favorite.publishedDate }}</p>
            <!--  -->
            <button class="cancelFavorite-button" @click.stop="confirmCancelFavorite(favorite)"></button>
          </div>
        </div>

        <!--  -->
        <div v-if="selectedText === ''" class="selectedoption">
          <div v-for="comment in userComments" :key="comment.commentId" class="post">
            <h3 class="comment-title">{{ comment.content }}</h3>


            <!--  -->
            <div class="comment-meta">
              <div class="comment-left">
                <span>{{ comment.username }}</span>
                <span>&nbsp;|&nbsp;</span>
                <span>{{ comment.createdDate }}</span>
              </div>
              <div class="comment-right">
                <span>{{ comment.title }}</span>
              </div>
            </div>
            <!--  @click.stop  -->
            <button class="delete-comment" @click.stop="deleteComment(comment)"></button>
          </div>
        </div>


      </div>


      <!--  -->
      <div v-if="showPostDialog" class="post-dialog">
        <div class="post-dialog-content">
          <div class="post-dialog-header">
            <h3>{{ selectedPost.title }}</h3>
            <!--  Unicode  -->
            <button @click="closePostDialog" class="close-button">&times;</button>
          </div>
          <div class="post-dialog-body">
            <p>{{ selectedPost.content }}</p>
          </div>
          <div class="post-dialog-footer">
            <p></p>
            <ul>
              <li v-for="comment in commentInPost" :key="comment.id">
                <span class="post-content">{{ comment.username }}:</span>
                <span class="post-content">{{ comment.content }}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>

      <!--  -->
      <div v-if="showConfirmDialog" class="confirm-dialog">
        <div class="confirm-dialog-content">
          <p></p>
          <button @click="deletepost(selectedPost)"></button>
          <button @click="cancelDelete"></button>
        </div>
      </div>

      <!--  -->
      <div v-if="showConfirmFavoriteDialog" class="confirm-dialog">
        <div class="confirm-dialog-content">
          <p></p>
          <button @click="cancelFavorite(selectedFavorite)"></button>
          <button @click="cancelDialog"></button>
        </div>
      </div>

      <!--  -->
      <div v-if="showConfirmCommentDialog" class="confirm-dialog">
        <div class="confirm-dialog-content">
          <p></p>
          <button @click="confirmComment(selectedComment)"></button>
          <button @click="cancelCommentDialog"></button>
        </div>
      </div>

      <!--  -->
      <div v-if="showChangePasswordDialog" class="password-dialog-overlay">
        <div class="password-dialog">
          <h3></h3>
          <div class="form-group">
            <label for="old-password"></label>
            <input type="password" id="old-password" v-model="oldPassword" />
          </div>
          <div class="form-group">
            <label for="new-password"></label>
            <input type="password" id="new-password" v-model="newPassword" />
          </div>
          <div class="form-group">
            <label for="confirm-password"></label>
            <input type="password" id="confirm-password" v-model="confirmPassword" />
          </div>
          <!--  -->
          <p v-if="passwordError" class="error-message">{{ passwordError }}</p>
          <div class="dialog-actions">
            <button class="confirm-button" @click="confirmChangePassword"></button>
            <button class="cancel-button" @click="cancelChangePassword"></button>
          </div>
        </div>
      </div>
      <!--  -->
      <div v-if="showAvatarSelection" class="avatar-selection-overlay">
        <div class="avatar-selection-container">
          <h3></h3>
          <div class="avatar-grid">
            <div v-for="(avatar, index) in avatarOptions" :key="index"
              :class="{ 'avatar-option': true, 'selected': selectedAvatar === avatar }" @click="selectAvatar(avatar)">
              <img :src="avatar" class="avatar-image" />
              <div v-if="selectedAvatar === avatar" class="checkmark"></div>
            </div>
          </div>
          <div class="edit-actions">
            <button class="confirm-button" @click="confirmAvatar"></button>
            <button class="cancel-button" @click="cancelAvatarSelection"></button>
