<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import Header from '@/home/Header.vue';
import Footer from '@/home/Footer.vue';
export default {
  components: {
    Header,
    Footer,
  },
  computed: {
    ...mapGetters({
      currentUser: 'getUserInfo', // 
      isAdmin: 'isAdmin', // 
      userAvatar: 'userAvatar', //  getter 
      isAuthenticated: 'isAuthenticated',// 
      getPosts: 'getPosts',
      userComment: 'commentsByUser',
    }),
    posts() {
      return this.userPosts;
    },
    commentInPost() {
      return this.postComment;
    },
  },
  data() {
    return {
      userComments: [],
      userPosts: [], // 
      userFavors: [],
      postComment: [],
      // favorites: [{
      //   favorite_id: 1,
      //   article_id: 101,
      //   article_title: "1",
      //   article_content: ",,,,,,,,,,,,",
      //   published_date: "2024-09-06"
      // },
      // {
      //   favorite_id: 2,
      //   article_id: 102,
      //   article_title: "2",
      //   article_content: "",
      //   published_date: "2024-09-05"
      // }
      // ],
      showChangePasswordDialog: false, // 
      passwordError: '',
      oldPassword: '',
      newPassword: '',
      confirmPassword: '',

      showPostDialog: false, // 

      showConfirmDialog: false, // 
      selectedPost: null, // 
      //
      showDropdown: false, // 
      selectedText: '', // 

      //
      //favorites: [ /*  */ ],
      showConfirmFavoriteDialog: false, // 
      selectedFavorite: null, // 

      selectedComment: null,//
      showConfirmCommentDialog: false, // 

      userTemp: {},
      editing: false,
      emailError: null,
      usernameError: null,
    };
  },
  //comments: [/**/ ],  // 
  mounted() {
    this.fetchUserPosts(this.currentUser.userId)
      .then(result => {
        console.log('fetchUserPosts :', result); // 
        this.userPosts = result; //  data 
      })
      .catch(error => {
        console.error(':', error);
      });

    this.$store.dispatch('fetchCommentsByUser', this.currentUser.userId)
      .then(result => {
        console.log(':', result); // 
        this.userComments = result; // 
      })
      .catch(error => {
        console.error(':', error);
      });

    this.getUserFavorites();

    // this.$store.dispatch('fetchFavoritesByUser', this.currentUser.userId)
    //   .then(result => {
    //     console.log(':', result); // 
    //     this.userFavors = result; // 
    //   })
    //   .catch(error => {
    //     console.error(':', error);
    //   });
  },
  methods: {
    ...mapActions({
      logout: 'logout',
      setAvatar: 'setAvatar',
      updateUserInfo: 'updateUserInfo',
      fetchUserPosts: 'fetchUserPosts',
      deletePost: 'deletePost',
    }),
    async fetchPostComments(articleId) {
      try {
        const response = await this.$store.dispatch('fetchCommentsByPost', articleId);
        this.postComment = response; // 
      } catch (error) {
        console.error(':', error);
      }
    },
    async getUserFavorites() {
      try {
          const result = await this.$store.dispatch('fetchFavoritesByUser', this.currentUser.userId);
          console.log(':', result);
          this.userFavors=result;        
      } catch (error) {
        console.error(':', error);
      }
    },
    editUserInfo() {
      this.userTemp = { ...this.currentUser };
      this.editing = true;
    },
    // 
    validateUsername() {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (emailPattern.test(this.userTemp.username)) {
        this.usernameError = '';
      } else {
        this.usernameError = null;
      }
    },
    // 
    validateEmail() {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailPattern.test(this.userTemp.email)) {
        this.emailError = '';
      } else {
        this.emailError = null;
      }
    },

    async confirmEdit() {
      if (!this.emailError && !this.usernameError && this.editing) {
        try {
          //  Vuex  action
          await this.updateUserInfo({
            username: this.userTemp.username,
            email: this.userTemp.email,
            password: this.userTemp.password,
            languagePreference: this.userTemp.languagePreference,
            bio: this.userTemp.bio,
            avatar: this.userTemp.avatar,
            gender: this.userTemp.gender
          });
          this.editing = false;
        } catch (error) {
          console.error(':', error);
          alert('');
        }
      }
    },
    toggleUserMenu() {
      this.showUserMenu = !this.showUserMenu;
    },
    handleUserAvatarClick() {
      if (this.currentUser.role === 'guest') {
        this.$router.push('/login'); // guest
      } else {
        this.goToUserProfile(); // 
      }
    },
    cancelEdit() {
      this.editing = false;  // 
      this.emailError = null;  // 
    },
    openAvatarSelection() {
      this.$refs.fileInput.click(); // 
    },
    handleFileUpload(event) {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.selectedAvatar = e.target.result; // Base64selectedAvatar
          this.updateUserAvatar(); // 
        };
        reader.readAsDataURL(file); // Base64
      }
    },
    async updateUserAvatar() {
      try {
        await this.updateUserInfo({
          username: this.currentUser.username,
          email: this.currentUser.email,
          password: this.currentUser.password,
          languagePreference: this.currentUser.languagePreference,
          bio: this.currentUser.bio,
          avatar: this.selectedAvatar,
          gender: this.currentUser.gender
        });
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },
    goToCatalog() {
      this.$router.push('/catalog');
    },
    goToCommunity() {
      this.$router.push('/community');;
    },
    goToAdminPanel() {
      this.$router.push('/adminpanel');
    },
    goHome() {
      this.$router.push('/home');
    },
    performLogout() {
      this.logout();
      this.$router.push('/login'); // 
    },
    goToUserProfile() {
      this.$router.push('/userprofile');;
    },

    //
    // 
    confirmDelete(post) {
      this.selectedPost = post; // 
      //console.log(this.selectedPost);
      this.showConfirmDialog = true; // 
    },
    // 
    deletepost(post) {
      // API
      this.deletePost(post.articleId);
      this.userPosts = this.userPosts.filter(p => p.articleId !== post.articleId);
      this.showConfirmDialog = false; // 
      this.selectedPost = null; // 
    },
    // 
    cancelDelete() {
      this.showConfirmDialog = false; // 
      this.selectedPost = null; // 
    },
    // 
    openPostDialog(post) {
      this.selectedPost = post;
      this.showPostDialog = true;
      //  post  articleId 
      this.fetchPostComments(post.articleId);
    },
    // 
    closePostDialog() {
      this.showPostDialog = false;
      this.selectedPost = null;
    },
    async confirmChangePassword() {
      // 
      this.passwordError = '';

      // 
      if (this.newPassword !== this.confirmPassword) {
        this.passwordError = '';
        return;
      }
      // 
      if (this.oldPassword !== this.currentUser.password) {
        this.passwordError = '';
        return;
      }

      // 
      if (this.newPassword === this.oldPassword) {
        this.passwordError = '';
        return;
      }


      //  8  20 
      if (this.newPassword.length < 8 || this.newPassword.length > 20) {
        this.passwordError = ' 8  20 ';
        return;
      }
      // 
      try {
        await this.updateUserInfo({
          username: this.currentUser.username,
          email: this.currentUser.email,
          password: this.newPassword,
          languagePreference: this.currentUser.languagePreference,
          bio: this.currentUser.bio,
          avatar: this.currentUser.avatar,
          gender: this.currentUser.gender
        });
        alert('');
        this.cancelChangePassword(); // 
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },
    cancelChangePassword() {
      this.showChangePasswordDialog = false; // 
      this.oldPassword = '';
      this.newPassword = '';
      this.confirmPassword = '';
      this.passwordError = '';
    },
    // 
    openFavoriteDialog(favorite) {
      this.selectedPost = favorite; //  selectedPost
      //this.comments = [ /*  */];
      this.showPostDialog = true; // 
      this.fetchPostComments(favorite.articleId);
    },
    // 
    confirmCancelFavorite(favorite) {
      this.selectedFavorite = favorite;
      this.showConfirmFavoriteDialog = true;
    },

    // 
    cancelFavorite(favorite) {
      console.log('favor:', favorite.favorId); // 
      this.$store.dispatch('deleteFavorite', favorite.favorId);
      //console.log('comment:', comment); // 
      this.userFavors = this.userFavors.filter(p => p.favorId !== favorite.favorId);
      this.showConfirmFavoriteDialog = false;
      this.selectedFavorite = null;
    },

    // 
    cancelDialog() {
      this.showConfirmFavoriteDialog = false;
      this.selectedFavorite = null;
    },
    confirmComment(comment) {
      //   
      this.$store.dispatch('deleteComment', comment.commentId);
      console.log('comment:', comment); // 
      this.userComments = this.userComments.filter(p => p.commentId !== comment.commentId);
      this.showConfirmCommentDialog = false;
      this.selectedComment = null;
    },
    deleteComment(comment) {
      this.selectedComment = comment;
      this.showConfirmCommentDialog = true;
    },

    // 
    cancelCommentDialog() {
      this.showConfirmCommentDialog = false;
      this.selectedComment = null;
    },
    //
    toggleDropdown() {
      this.showDropdown = !this.showDropdown; // 
    },
    selectOption(option) {
      this.selectedText = option; // 
      this.showDropdown = false; // 
    },
  }
};
</script>



